body{
    max-width: 800px;
    margin-right: auto;
    margin-left: auto;
}
ul{
    list-style: none;
    padding: 0;
}
h1,h2,h3{
    margin: 0 0 30px 0;
    text-align: center;
}
.contactList{
    display: flex;
    max-width: 800px;
    flex-wrap: wrap;
}
.contactCard{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    width: 250px;
    height: 50px;
    background-color: rgb(157, 157, 238);
    color: black;
    text-align: center;
    border-radius: 5px;
    margin-right: 10px;
    margin-bottom: 10px;
}
/* .contsctListContainer{

} */
.input{
    display: flex;
    margin-bottom: 20px;
    
}

.inputValue{
    margin-left: 10px;
}
.buttonAddContact{
    
    display: block ;
    border: none;
    border-radius: 6px;
    width: 100%;
    height: 50px;
    margin-right: auto;
    margin-left: auto;
    background-color: rgb(157, 157, 238);

}
.form{
    max-width: 800px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 30px;
    padding: 20px;
    border: 2px solid black;
}